
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap');

*{
    box-sizing: border-box;
}

body{
    margin: 0;
    font-family: "ubuntu";
    background-color: #fcae76;
}

.nav-bar{
    background-color: #FF7F51;
    background: linear-gradient(to right, #ff6546, #FF9B54);
    height: 100px;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0px 2px 7px rgba(0, 0, 0, .2);
}

.logo-name{
    margin: 0;
    margin-left: 30px;
    font-size: 3rem;
    color: #333;
    -webkit-text-stroke: 1px black;
}

.logo-name > span{
    color: red;
    -webkit-text-stroke: 1px black;

}

.question-mark{
    width: 50px;
    height: 50px;
    margin-right: 20px;
    cursor: pointer;
}

.main-bar{
    background-color: aliceblue;
    margin-top: 20px;
    margin-left: 5%;
    height: 80vh;
    width: 90vw;
    border-radius: 10px;
    text-align: center;
    color: #333;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    flex-direction: column;
}

.main-img{
    width: 200px;
    margin-bottom: 40px;
}

.main-par{
    margin: 0;
}

.main-input{
    width: 100%;
}
.main-input > input{
    width: 40%;
}

.input-box {
    border-radius: 7px 0 0 7px;
    outline: 1px solid #FEBF00;
    border: 0;
    font-family: "ubuntu";
    background-color: #efefef;
    outline-offset: 3px;
    padding: 5px .7rem;
    transition: 0.25s;
    color: #333;
}

.input-box:focus {
    outline-offset: 5px;
    background-color: #fff
}


.gen-button{
    height: 32px;
    margin-left: 4px;
    font-family: "ubuntu";
    font-size: 14px;
    text-align: center;
    letter-spacing: 0.5px;
    color: #333;
    background-color: #FF9B54;
    border: 0;
    box-shadow: none;
    border-radius: 0 7px 7px 0;
    padding-left: 12px;
    padding-right: 12px;
    font-weight: bold;
    cursor: pointer;
    transition: .3s;
}

.gen-button:hover{
    background-color: #FEBF00;
}

.radios{
    display: flex;
    margin-top: 20px;
}

.radio-btn {
    display: flex;
    flex-direction: column;
    margin: 20px;
}

.radio-content > img{
    max-width: 100px;
    margin-bottom: 15px;

}

.kitti{
    margin-top: 20px;
    width: 80px;
}

.radio-btn > input{
    opacity: 0;
    position: absolute;
    pointer-events: none;
}


.radio-input:checked ~ .radio-content {
    background-color: #FF9B54;
    border-radius: 40px 40px 10px 10px;
}




.load-page{
    background-color: #222;
    position: absolute;
    width: 100%;
    height: 100vh;
    color: #efefef;
    transition: 1s;
    display: flex;
    justify-content: center;
    align-items: center;
    background : linear-gradient(135deg, #111, #333)
}


.master-container {
    margin: 0;
    padding: 100px 40px;
    background-color: #131516;
    display: grid;
    border-radius: 13px;
    transform: scale(0.7);
    background : linear-gradient(45deg, #111, #333)
  }
  
  .container {
    width: 300px;
    height: 300px;
    position: relative;
    margin: auto;
    border-radius: 50%;
    border: 2px solid white;
    overflow: hidden;
  }
  
  .crescent {
    position: relative;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    left: calc(50% - 25px);
    top: 12px;
    overflow: hidden;
    animation: rotate 3s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .crescent span:nth-child(2) {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 0 0 2px white;
    border-radius: 50%;
  }
  
  .crescent span:nth-child(1) {
    background-color: #131516;
    position: absolute;
    left: 40%;
    display: block;
    width: calc(70% - 6px);
    height: calc(70% - 6px);
    box-shadow: inset 0 0 0 2px white;
    border-radius: 50%;
    z-index: 9;
  }
  
  .star {
    position: absolute;
    overflow: hidden;
    border-radius: 50%;
    height: 20px;
    width: 20px;
  }
  
  .star span:nth-child(1)::before {
    content: "";
    border: 2px solid white;
    height: 50%;
    width: 50%;
    border-top-color: transparent;
    border-left-color: transparent;
    border-radius: 0% 0% 100% 10%;
    display: block;
    left: -3px;
    top: -3px;
    position: absolute;
  }
  
  .star span:nth-child(1)::after {
    content: "";
    border: 2px solid white;
    height: 50%;
    width: 50%;
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-radius: 0% 100% 0% 0%;
    display: block;
    left: -3px;
    bottom: -3px;
    position: absolute;
  }
  
  .star span:nth-child(2)::before {
    content: "";
    border: 2px solid white;
    height: 50%;
    width: 50%;
    border-top-color: transparent;
    border-right-color: transparent;
    border-radius: 0% 0% 0% 100%;
    display: block;
    right: -3px;
    top: -3px;
    position: absolute;
  }
  
  .star span:nth-child(2)::after {
    content: "";
    border: 2px solid white;
    height: 50%;
    width: 50%;
    border-bottom-color: transparent;
    border-right-color: transparent;
    border-radius: 100% 0% 0% 0%;
    display: block;
    right: -3px;
    bottom: -3px;
    position: absolute;
  }
  
  .small-star {
    position: absolute;
    overflow: hidden;
    border-radius: 50%;
    height: 12px;
    width: 12px;
  }
  
  .small-star span:nth-child(1)::before {
    content: "";
    border: 1.5px solid white;
    height: 50%;
    width: 50%;
    border-top-color: transparent;
    border-left-color: transparent;
    border-radius: 0% 0% 100% 10%;
    display: block;
    left: -2px;
    top: -2px;
    position: absolute;
  }
  
  .small-star span:nth-child(1)::after {
    content: "";
    border: 1.5px solid white;
    height: 50%;
    width: 50%;
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-radius: 0% 100% 0% 0%;
    display: block;
    left: -2px;
    bottom: -2px;
    position: absolute;
  }
  
  .small-star span:nth-child(2)::before {
    content: "";
    border: 1.5px solid white;
    height: 50%;
    width: 50%;
    border-top-color: transparent;
    border-right-color: transparent;
    border-radius: 0% 0% 0% 100%;
    display: block;
    right: -2px;
    top: -2px;
    position: absolute;
  }
  
  .small-star span:nth-child(2)::after {
    content: "";
    border: 1.5px solid white;
    height: 50%;
    width: 50%;
    border-bottom-color: transparent;
    border-right-color: transparent;
    border-radius: 100% 0% 0% 0%;
    display: block;
    right: -2px;
    bottom: -2px;
    position: absolute;
  }
  
  .s1 {
    left: 60px;
    top: 50px;
    animation: glow 1.1s 0.9s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .s2 {
    right: 35px;
    bottom: 155px;
    animation: glow 1.3s 0.5s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .s3 {
    left: calc(50% - 10px);
    top: 75px;
    animation: glow 1.5s 0.3s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .s4 {
    left: 35px;
    bottom: 155px;
    animation: glow 3s 0.7s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .s5 {
    right: 60px;
    top: 50px;
    animation: glow 2s 1.2s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .ss1 {
    right: 15px;
    bottom: 130px;
    animation: glow 2s 1s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .ss2 {
    left: 15px;
    bottom: 130px;
    animation: glow 1s 0s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .ss3 {
    left: 35px;
    bottom: 195px;
    animation: glow 1.9s 0.1s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .ss4 {
    right: 35px;
    bottom: 195px;
    animation: glow 1.6s 0.9s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .ss5 {
    left: 100px;
    bottom: 180px;
    animation: glow 1s 0.5s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .ss6 {
    right: 100px;
    bottom: 180px;
    animation: glow 1.7s 0.6s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .ss7 {
    right: 90px;
    top: 30px;
    animation: glow 0.8s 0.8s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .ss8 {
    left: 90px;
    top: 30px;
    animation: glow 1.8s 0.3s cubic-bezier(0.15, 0.83, 0.66, 1) infinite;
  }
  
  .hill {
    display: block;
    width: 140%;
    height: 100%;
    border: 2px solid white;
    border-radius: 50%;
    bottom: -40%;
    position: relative;
    left: -20%;
    background-size: 3%;
    background-repeat: repeat;
    opacity: 0.7;
  }
  
  .fire-pit {
    width: 18px;
    height: 30px;
    position: absolute;
    left: calc(50% - 9px);
    bottom: 25px;
    z-index: 99;
  }
  
  .fire {
    width: 0;
    height: 0;
    border: 9px solid transparent;
    border-bottom: 18px solid #ffffff;
    position: relative;
    top: -9px;
  }
  
  .fire:after {
    content: "";
    position: absolute;
    left: -9px;
    top: 18px;
    width: 0;
    height: 0;
    border: 9px solid transparent;
    border-top: 9px solid #ffffff;
  }
  
  .inner-fire {
    width: 0;
    height: 0;
    border: 4.5px solid transparent;
    border-bottom: 9px solid #131516;
    top: 4.5px;
    position: absolute;
    z-index: 9;
    left: -4.5px;
  }
  
  .inner-fire:after {
    content: "";
    position: absolute;
    left: -4.5px;
    top: 9px;
    width: 0;
    height: 0;
    border: 4.5px solid transparent;
    border-top: 6px solid #131516;
  }
  
  .log1 {
    background-color: white;
    height: 2px;
    transform: rotate(35deg);
    transform-origin: center;
    position: relative;
    left: -3px;
    bottom: -1.5px;
  }
  
  .log2 {
    background-color: white;
    height: 2px;
    transform: rotate(-35deg);
    transform-origin: center;
    left: 3px;
    position: relative;
    bottom: -0px;
  }
  
  .hut {
    width: 0;
    height: 0;
    border-left: 38px solid transparent;
    border-right: 38px solid transparent;
    border-bottom: 75px solid #131516;
    position: absolute;
    left: calc(50% - 37.6px);
    z-index: 11;
    bottom: 80px;
    transform: scale(1.3);
  }
  
  .hut hr:nth-child(1) {
    height: 0px;
    position: absolute;
    width: 75px;
    bottom: -75px;
    left: calc(50% - 37.6px);
  }
  
  .hut hr:nth-child(2) {
    height: 0px;
    position: absolute;
    width: 96px;
    bottom: -32px;
    left: -32px;
    transform: rotate(63.5deg);
    border: 1px solid white;
  }
  
  .hut hr:nth-child(3) {
    height: 0px;
    position: absolute;
    width: 96px;
    bottom: -32px;
    right: -32px;
    transform: rotate(-63.5deg);
    border: 1px solid white;
  }
  
  .hut hr:nth-child(4) {
    height: 0px;
    position: absolute;
    width: 60px;
    bottom: -60px;
    left: -30px;
    border: 0.5px solid white;
  }
  
  .hut hr:nth-child(5) {
    height: 0px;
    position: absolute;
    width: 55px;
    bottom: -55px;
    left: -27.5px;
    border: 0.5px solid white;
  }
  
  .hut hr:nth-child(6) {
    height: 0px;
    position: absolute;
    width: 50px;
    bottom: -50px;
    left: -25px;
    border: 0.5px solid white;
  }
  
  .doorway {
    width: 0;
    height: 0;
    border-left: 22px solid transparent;
    border-right: 22px solid transparent;
    border-bottom: 42px solid #131516;
    position: relative;
    left: -22px;
    bottom: -32px;
  }
  
  .doorway::before {
    content: "";
    border-left: 1px solid white;
    width: 0px;
    height: 48px;
    display: inline-block;
    transform: rotate(-27deg);
    left: 11px;
    position: absolute;
    bottom: -45px;
  }
  
  .doorway::after {
    content: "";
    border-left: 1px solid white;
    width: 0px;
    height: 48px;
    display: inline-block;
    transform: rotate(27deg);
    left: -11px;
    position: absolute;
    bottom: -45px;
  }
  
  .doorway > hr.split {
    height: 44px;
    width: 1px;
    position: absolute;
    left: 0px;
    top: 0;
    display: inline-block;
    background-color: white;
    border: 0;
  }
  
  /* Trees */
  .tree {
    position: absolute;
    width: 25px;
    height: 48px;
    z-index: 21;
    transform: scale(1.3);
  }
  
  .tree .trunc {
    height: 48px;
    width: 1px;
    background-color: white;
    left: 12.5px;
    position: absolute;
    margin-top: 1.5px;
  }
  
  .tree .leaves {
    height: 18px;
    width: 18px;
    transform: rotate(45deg);
    border-left: 1.5px solid white;
    border-top: 1.5px solid white;
    border-bottom: 0;
    border-right: 0;
    margin-bottom: -15px;
    margin-top: 5px;
    left: 4px;
    position: relative;
  }
  
  .one {
    left: 30px;
    bottom: 90px;
  }
  
  .two {
    right: 30px;
    bottom: 90px;
  }
  
  .three {
    left: 80px;
    bottom: 115px;
  }
  
  .four {
    right: 80px;
    bottom: 115px;
  }
  
  .loading-text {
    color: white;
    margin-top: 40px;
    font-size: 1.8rem;
    font-weight: bold;
    font-family: 'Inter', sans-serif;
    display: block;
    text-align: center;
    padding: 20px 0 0 0;
    opacity: 0.8;
  }
  
  @keyframes glow {
    0% {
      transform: scale(0);
    }
  
    50% {
      transform: scale(1);
    }
  
    100% {
      transform: scale(0);
    }
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0);
    }
  
    50% {
      transform: rotate(10deg);
    }
  
    100% {
      transform: rota(0);
    }
  }